.button-arrow {
   --size: 70px;
   display: flex;
   align-items: center;
   justify-content: center;
   min-width: var(--size);
   width: var(--size);
   height: var(--size);
   color: #ffffff;
   clip-path: polygon(0% 100%, 0% 20%, 20% 0%, 100% 0%, 100% 80%, 80% 100%);
   background-color: #DB0E0E;
   transition: background-color 0.3s;

   svg {
      width: 34.28%;
      height: auto;
   }
}

a.button-arrow,
button.button-arrow {
   &:hover {
      background-color: #1C1C1C;
   }
}